* {
  box-sizing: border-box;
  margin: 0;
}

body {
  background-color: #f7f7f7;
  min-height: 100vh;
}

.section {
  flex-direction: column;
  justify-content: center;
  height: 1000px;
  margin: 0 auto;
  display: flex;
}

@media (width >= 700px) {
  .section {
    flex-direction: column;
    justify-content: center;
    display: flex;
  }
}

@media (width >= 900px) {
  .section {
    flex-direction: column;
    justify-content: center;
    display: flex;
  }
}

.section_fonts {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

p {
  text-align: center;
  color: #333;
  margin-top: 50px;
  font-family: Lato, sans-serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 100;
  line-height: 1.6;
}

h1 {
  text-align: center;
  text-shadow: 2px 2px 4px #555;
  color: #ffde00;
  font-family: "Press Start 2P", system-ui;
  font-size: 38px;
  font-style: normal;
  font-weight: 600;
}

@media (width >= 1000px) {
  h1 {
    font-size: 40px;
  }

  p {
    font-size: 25px;
  }
}

.content_input_and_button {
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.group {
  align-items: center;
  margin-top: 100px;
  line-height: 28px;
  position: relative;
}

.input {
  color: #333;
  cursor: text;
  z-index: 0;
  background-color: #fff;
  border: 1px solid #333;
  border-radius: 12px;
  outline: none;
  width: 300px;
  height: 45px;
  margin: 0 10px;
  padding: 10px;
  font-family: Roboto, sans-serif;
  font-size: 15px;
  font-weight: bold;
  transition: all .25s cubic-bezier(.19, 1, .22, 1);
  box-shadow: 0 4px 8px #0003;
}

@media (width >= 1000px) {
  .input {
    width: 500px;
    height: 45px;
  }
}

.input::placeholder {
  color: #333;
}

.input:hover {
  box-shadow: 0 4px 8px #0003, 0 0 10px #fc06;
}

.input:active {
  transform: scale(.95);
}

.input:focus {
  box-shadow: 0 0 0 2.5px #333;
}

.search-icon {
  fill: #333;
  pointer-events: none;
  z-index: 1;
  width: 1.3rem;
  height: 1.3rem;
  position: absolute;
  left: 1.3rem;
}

.button {
  color: #fff;
  cursor: pointer;
  background: #c00;
  border: 1px solid #333;
  border-radius: .5em;
  width: 75px;
  height: 45px;
  margin-right: 5px;
  padding: 10px;
  font-family: Roboto, sans-serif;
  font-size: 15px;
  font-weight: bold;
  transition: all .3s;
  box-shadow: 0 4px 8px #0003;
}

@media (width >= 1000px) {
  .button {
    width: 150px;
    height: 45px;
  }
}

.button:hover {
  box-shadow: 0 4px 8px #0003, 0 0 10px #c00;
}

.button:active {
  transform: scale(.95);
}

#resultado {
  backdrop-filter: blur(6px);
  cursor: pointer;
  user-select: none;
  color: #000;
  background: #fafafa;
  border-radius: 17px;
  flex-direction: column;
  justify-content: center;
  width: 400px;
  margin: 50px auto 5px;
  font-weight: bolder;
  transition: all .5s;
  display: flex;
  box-shadow: 12px 17px 51px #00000038;
}

@media (width <= 400px) {
  #resultado {
    width: 350px;
  }
}

@media (width <= 360px) {
  #resultado {
    width: 350px;
  }
}

#resultado:hover {
  border: 1px solid #ddd;
  transform: scale(1.05);
}

#resultado:active {
  transform: scale(.95)rotateZ(1.7deg);
}
